const Container = styled.div`
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 1fr;
  height: 100vh;
  overflow: hidden;
`

const ImgTitle = styled.img`
  grid-column: 2 / 12;
  width: 100%;
  padding: 20px 0px;
`
// Use `posed.div` elements anywhere on the pages.

const Transition = posed.div({
  preEnter: {
    position: 'fixed',
    x: '100%',
    overflow: 'hidden',
    transition: {
        duration: 0,
        ease: 'easeInOut'
        }
},
  enter: {
    position: 'fixed',
    opacity: 1,
    overflow: 'hidden',
    x: '0%',
    transition: {
      duration: 800,
      ease: 'easeInOut'
    } 
  },
  exit: {
    position: 'fixed',
    opacity: 1,
    overflow: 'hidden',
    x: '-100%',
    transition: {
      duration: 800,
      ease: 'easeInOut'
    }
  },
})

